<template>
    <WebHeader/>
    <div id="user-center">
        <h3>提交宿舍订单</h3>
        <form>
            <div class="form-select">
                <label>5号楼1单元<input type="radio" v-model="unit" value="1"/></label>
                <label>5号楼2单元<input type="radio" v-model="unit" value="2"/></label>
                <label>8号楼1单元<input type="radio" v-model="unit" value="3"/></label>
                <label>8号楼2单元<input type="radio" v-model="unit" value="4"/></label>
            </div>
            <div class="form-elem">
                <span>同住人1：</span>
                <input v-model="order_roommate1" type="text" placeholder="输入同住人1">
            </div>
            <div class="form-elem">
                <span>同住人2：</span>
                <input v-model="order_roommate2" type="text" placeholder="输入同住人1">
            </div>
            <div class="form-elem">
                <span>同住人3：</span>
                <input v-model="order_roommate3" type="text" placeholder="输入同住人1">
            </div>
            <div class="form-elem">
                <button v-on:click.prevent="roomSelect" >提交</button>
            </div>
        </form>
    </div>
</template>

<script>
    import axios from 'axios';
    import WebHeader from '@/components/WebHeader.vue';

    import authorization from '@/utils/authorization';

    export default {
        name: 'PlaceOrder',
        components: {WebHeader},
        data: function () {
            return {
                unit: '',
                order_roommate1: '',
                order_roommate2: '',
                order_roommate3: '',
                order_response: null,
            }
        },
        methods: {
            roomSelect() {
                const that = this;
                // 验证登录状态
                authorization()
                    .then(function (response) {
                        // 检查登录状态
                        // 若登录已过期则不执行后续操作
                        if (!response[0]) {
                            alert('登录已过期，请重新登录');
                            return
                        }
                        console.log('room selection start');
                        // 密码不能小于 6 位
                        // 发送更新数据到接口
                        let data = {};
                        data.unit_id = that.unit;
                        data.roommate1 = that.order_roommate1;
                        data.roommate2 = that.order_roommate2;
                        data.roommate3 = that.order_roommate3;
                        const token = localStorage.getItem('access.myblog');
                        axios
                            .post('/api/select/', data, {
                                headers: {Authorization: 'Bearer ' + token},
                            })
                            .then(function (response) {
                                that.order_response = response.data;
                                alert('宿舍选择成功！');
                            })
                            .catch(function (error) {
                                console.log(error.response.data);
                                console.log(error.response.status);
                                console.log(error.response.headers);
                                alert(error.response.data);
                            });
                    });
            }
        },
    }
</script>

<style scoped>
    #user-center {
        text-align: center;
    }
    .form-elem {
        padding: 10px;
    }
    input {
        height: 25px;
        padding-left: 10px;
    }
    button {
        height: 35px;
        cursor: pointer;
        border: none;
        outline: none;
        background: gray;
        color: whitesmoke;
        border-radius: 5px;
        width: 200px;
    }
    form-select{
        margin-top: 20px;
    }
</style>