<template>
    <WebHeader/>
    <div class="room-title">
        {{ info.room_id }} {{ info.username }}
    </div>
</template>

<script>
    import axios from 'axios';
    import WebHeader from '@/components/WebHeader.vue';

    export default {
        name: 'OrderResult',
        components: {WebHeader},
        data: function () {
            return {
                info: []
            }
        },
        mounted() {
            this.getResponse();
        },
        methods: {
            getResponse() {
                const token = localStorage.getItem('access.myblog');
                axios
                    .get('/api/result/',{
                        headers: {'Authorization': 'Bearer ' + token}
                    })
                    .then(response => (this.info = response.data))
                }
            },
        }
</script>

<style>
    .room-title {
        font-size: large;
        font-weight: bolder;
        color: black;
        text-decoration: none;
        padding: 5px 0 5px 0;
    }
</style>